import { Footer } from "./components/Footer/Footer";
import { Header } from "./components/Header/Header";
import { Main } from "./components/Main/Main";
import { Route, Routes } from "react-router";
import { SingleUser } from "./components/SingleUser/SingleUser";
import { SingleRepo } from "./components/SingleRepo/SingleRepo";
import "./App.scss";


function App() {
  return (
    <div className="App">
      <Header />
        <Routes>
          <Route path="/react-github-search-app/" element={<Main />} />
          <Route path="/react-github-search-app/home" element={<Main />} />
          <Route path="/react-github-search-app/:user" element={<SingleUser />} />
          <Route path="/react-github-search-app/:user/:id" element={<SingleRepo />} />
        </Routes>
      <Footer />
    </div>
  );
}

export default App;
